<template>
  <article class="news-item">
    <div class="news-content">
      <div  class="news-cover" :class="{showCover: hasbg}" :style="{backgroundImage:'url('+ cover.bg +')'}">
        <p class="news-title">{{cover.title}}</p>
        <span class="news-cover-source">{{cover.source}}</span>
      </div>
      <div v-html="html"></div>
    </div>
  </article>
</template>

<script>
  export default {
    data() {
      return {
        html: '',
		hasbg: false,
		bgurl: 'data:img/jpg;base64,',
        cover: {}
      }
    },
    created: function () {
      this.fillCont()
    },
    methods: {
      fillCont: function () {
        this.$http.get('http://news-at.zhihu.com/api/4/news/' + this.$route.params.id, {}, {
          headers: {
            'X-Requested-With': 'XMLHttpRequest'
          },
          emulateJSON: true
        }).then(function (respones) {
          this.html = respones.data.body
		  if(respones.data.images){
			this.hasbg = true
			this.cover.bg = respones.data.images
		  }else{
			this.hasbg = false
			this.cover.bg = this.bgurl
		  }
          this.cover.title = respones.data.title
		  if(respones.data.image_source){
			this.cover.source = respones.data.image_source
		  }
        }, function (respones) {
          console.log(respones)
        })
      }
    }

  }

</script>

